import { Routes } from '@angular/router';
import { HomeComponent } from '../../home/home.component';
import { UserComponent } from '../../user/user.component';
import { TablesComponent } from '../../tables/tables.component';
import { TypographyComponent } from '../../typography/typography.component';
import { IconsComponent } from '../../icons/icons.component';
import { MapsComponent } from '../../maps/maps.component';
import { NotificationsComponent } from '../../notifications/notifications.component';
import { UpgradeComponent } from '../../upgrade/upgrade.component';
import { ManagerDataComponent } from './pages/manager-data/manager-data.component';
import { ManageAccountComponent } from './pages/manager-data/manage-account/manage-account.component';
import { ManagePostComponent } from './pages/manager-data/manage-post/manage-post.component';
import { ManageReportsComponent } from './pages/manager-data/manage-reports/manage-reports.component';
import { ManagePackagesComponent } from './pages/manager-data/manage-packages/manage-packages.component';
import { ManageTransactionsComponent } from './pages/manager-data/manage-transactions/manage-transactions.component';
import { ManageRulesComponent } from './pages/manager-data/manage-rules/manage-rules.component';
import { ManageNewComponent } from './pages/manager-data/manage-new/manage-new.component';

export const AdminLayoutRoutes: Routes = [
    { path: 'dashboard', component: HomeComponent },
    { path: 'user/:username', component: UserComponent },
    { path: 'icons', component: IconsComponent },
    {
        path: 'manager_data',
        component: ManagerDataComponent,
        children: [
            {
                path: 'managementaccount',
                component: ManageAccountComponent
            },
            {
                path: 'managementpost',
                component: ManagePostComponent
            },
            {
                path: 'managementpackages',
                component: ManagePackagesComponent
            },
            {
                path: 'managementtransactions',
                component: ManageTransactionsComponent
            },
            {
                path: 'managementreports',
                component: ManageReportsComponent
            },
            {
                path: 'managementrules',
                component: ManageRulesComponent
            },
            {
                path: 'managementnews',
                component: ManageNewComponent
            }



        ]
    },
];
