import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DataServices } from 'app/core/services/data.service';
import { error } from 'console';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {
  username: string;
  user:any={};
  constructor(
    private dataService: DataServices,
    private activeRouter: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit() {
    this.username = this.activeRouter.snapshot.params['username'];
   // this.userCurrent=JSON.parse(localStorage.getItem("user"));
    this.dataService.getAccountByUserName(this.username).subscribe(res=>{
      console.log(res);
      this.user= res.accountDto;
    }, error=>console.log(error));

  }
  save(){
    this.dataService.updateAccount(this.user,this.username).subscribe(res=>{
      if(res.message=="Account updated successfully"){
        this.router.navigate(['/dasboard']);
      }else{
        return;
      }
      
    },error=>console.log(error));
  }
}
