import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DataServices } from 'app/core/services/data.service';
import { NotificationService } from 'app/core/services/notification';

@Component({
  selector: 'app-manage-reports',
  templateUrl: './manage-reports.component.html',
  styleUrls: ['./manage-reports.component.css']
})
export class ManageReportsComponent implements OnInit {
  reportname: string;
  reports: any = [];
  reportSelect: string;
  page: number = 0;
  pageSize: number;
  totalElements: number;
  result: any = {};

  reportDetail: any = {
    id: -1,
    title: "",
    email: "",
    name: "",
    description: "",
    createdAt: "",
    post_id: "",
    status: ""
  }
  constructor(
    private dataService: DataServices,
    private activeRouter: ActivatedRoute,
    private router: Router,
    private noti: NotificationService) {
    this.getListReport();
  }


  //display

  displayModalView: boolean = false;
  displayModalEdit: boolean = false;
  confirmModal: boolean = false;
  confirmModalActive: boolean = false;
  confirmModalDeactive: boolean = false;

  view(id) {
    this.displayModalView = true;
    for (let item of this.reports) {
      if (item.id == id) {
        this.reportDetail = item;
        return;
      }
    }
  }
  deactive(id) {
    this.confirmModalDeactive = true;
    for (let item of this.reports) {
      if (item.id == id) {
        this.reportDetail = item;
        return;
      }
    }
  }

  active(id) {
    this.confirmModalActive = true;
    for (let item of this.reports) {
      if (item.id == id) {
        this.reportDetail = item;
        this.reportSelect = item.id;
        return;
      }
    }
  }
  ngOnInit() {

    // this.userCurrent=JSON.parse(localStorage.getItem("user"));
    this.getListReport();
  }

  paginate(event) {
    this.page = event.page;
    this.getListReport();
  }
  public getListReport(): void {

    this.dataService.getAllReport(this.page).subscribe((res) => {
      // this.spinner.hide();
      this.reports = res.content;
      this.pageSize = res.totalPages;
      this.totalElements = res.totalElements;
      console.log(res);
    }, error => console.log(error));
  }

  public deactiveReports() {
    this.dataService.deactiveReport(this.reportDetail.id).subscribe(res => {
      if (res.message == "Deactivate report successfully") {
        this.confirmModalDeactive = false;
        this.noti.SuccessNotification("Hy kch hot thnh cng !")
      } else {
        this.noti.ErrorNotification("Hy kch hot tht bi !")
      }
    }, error => console.log(error));
  }

  public activeReports() {
    this.dataService.activeReport(this.reportDetail.id).subscribe(res => {
      if (res.message == "Report was approved") {
        this.confirmModalActive = false;
        this.noti.SuccessNotification("Kch hot thnh cng !")
      } else {
        this.noti.ErrorNotification("Kch hot tht bi !")
      }
    }, error => console.log(error));
  }


  //   public updateReports() {
  //     this.dataService.updateReport(this.reportDetail,this.reportSelect).subscribe(res => {
  //       if (res.message == "Update report successfully") {
  //         this.displayModalEdit = false;
  //       } else {
  //         return;
  //       }
  //     }, error => console.log(error));
  //   }
}

