import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DataServices } from 'app/core/services/data.service';
import { NotificationService } from 'app/core/services/notification';

@Component({
  selector: 'app-manage-account',
  templateUrl: './manage-account.component.html',
  styleUrls: ['./manage-account.component.css']
})
export class ManageAccountComponent implements OnInit {

  username: string;
  user: any = [];
  usernameSelect: string;
  page: number = 1;
  pageSize: number;
  totalElements: number;



  userDetail: any = {
    id: -1,
    username: "",
    email: "",
    address: "",
    dob: "",
    full_name: "",
    gender: true,
    phone: "",
    status: true,
    image_profile: "",
    created_date: "",
    role: ""
  }
  constructor(
    private dataService: DataServices,
    private activeRouter: ActivatedRoute,
    private router: Router,
    private noti: NotificationService
  ) { }


  //display
  displayModalView: boolean = false;
  displayModalEdit: boolean = false;
  selected: boolean = true;
  confirmModalDeactive: boolean = false;
  confirmModalActive: boolean = false;

  view(id) {
    this.displayModalView = true;
    for (let item of this.user) {
      if (item.account_id == id) {
        this.userDetail = item;
        return;
      }
    }
  }
  deactive(id) {
    this.confirmModalDeactive = true;
    for (let item of this.user) {
      if (item.account_id == id) {
        this.userDetail = item;
        this.usernameSelect = item.username;
        return;
      }
    }
    
  }

  active(id) {
    this.confirmModalActive = true;
    for (let item of this.user) {
      if (item.account_id == id) {
        this.userDetail = item;
        this.usernameSelect = item.username;
        return;
      }
    }
  }

  edit(id) {
    this.displayModalEdit = true;
    for (let item of this.user) {
      if (item.account_id == id) {
        this.userDetail = item;
        this.usernameSelect = item.username;
        return;
      }
    }
  }
  ngOnInit() {
    this.username = this.activeRouter.snapshot.params['username'];
    // this.userCurrent=JSON.parse(localStorage.getItem("user"));
    this.getListAccount();
  }
  paginate(event) {
    //event.first = Index of the first record
    //event.rows = Number of rows to display in new page
    //event.page = Index of the new page
    //event.pageCount = Total number of pages
    this.page = event.page + 1;
    this.getListAccount();
  }
  public getListAccount(): void {
    this.dataService.getAccountList(this.page-1).subscribe((res) => {
      // this.spinner.hide();
      this.user = res.content;
      this.pageSize = res.totalPages;
      this.totalElements = res.totalElements;
    }, error => console.log(error));
  }

  public activeAccounts() {
      console.log(this.usernameSelect);
       this.dataService.activeAccount(this.usernameSelect).subscribe(res => {
      if (res) {
        this.confirmModalActive = false;
        this.noti.SuccessNotification("Kch hot thnh cng !");
        this.router.navigate(["/manager_data/managementaccount"]);
      } else {
        this.noti.ErrorNotification("Kch hot tht bi !")
      }
    }, error => console.log(error));
  }

  public deactiveAccounts() {
    this.dataService.deactiveAccount(this.usernameSelect).subscribe(res => {
      if (res.message == "Account deactived successfully") {
        this.confirmModalDeactive = false;
        this.noti.SuccessNotification("Hy kch hot thnh cng !")
      } else {
        this.noti.ErrorNotification("Hy kch hot tht bi !")
      }
    }, error => console.log(error));
  }


  public saveAccount() {
    console.log(this.userDetail);
    this.dataService.updateAccount(this.userDetail, this.usernameSelect).subscribe(res => {
      if (res.message == "Account updated successfully") {
        this.displayModalEdit = false;
        this.noti.SuccessNotification("Thay i thng tin thnh cng !")
      } else {
        this.noti.ErrorNotification("Thay i thng tin tht bi !")
      }
    }, error => console.log(error));
  }
}
