import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DataServices } from 'app/core/services/data.service';
import { NotificationService } from 'app/core/services/notification';

@Component({
  selector: 'app-manage-new',
  templateUrl: './manage-new.component.html',
  styleUrls: ['./manage-new.component.css']
})
export class ManageNewComponent implements OnInit {

  newsName: string;
  news: any = [];
  newsList: any = [];
  newsSelected: any = [];
  page: number = 0;
  pageSize: number;
  totalElements: number;
  newsphongthuy: any = [];
  newstuvan: any = [];
  newsTypeSelect: any = {};
  newsType: any = [];
  newsDetail: any = {
    title: "",
    content: "",
    createdAt: "",
    newImages: [],
    type: {
      id: 0,
      new_type: ""
    }
  }
  addNews: any = {
    title: "",
    content: "",
    newImages: [],
    type: {
      id: 0,
      new_type: ""
    }
  }

  //https://s3.us-east-2.amazonaws.com/test-aws1233321/1618339433144-Evening_Forests_Mountains_Firewatch_Campo_Santo_549147_1920x1080.jpg
  selectType() {
    this.addNews.type.new_type = this.newsTypeSelect;

  }
  constructor(
    private dataService: DataServices,
    private activeRouter: ActivatedRoute,
    private router: Router,
    private noti: NotificationService
  ) {
    this.getListNews();
  }

  view(id) {
    this.displayModalView = true;
    for (let item of this.news) {
      if (item.id == id) {
        this.newsDetail = item;
        return;
      }
    }
  }
  edit(id) {
    this.displayModalEdit = true;
    for (let item of this.news) {
      if (item.id == id) {
        this.newsDetail = item;

        return;
      }
    }
  }
  confirm(id) {
    this.confirmModal = true;
    for (let item of this.news) {
      if (item.id == id) {
        this.newsDetail = item;
        return;
      }
    }
  }

  //display
  displayModalView: boolean = false;
  displayModalEdit: boolean = false;
  displayModalAdd: boolean = false;
  confirmModal: boolean = false;
  confirmModalActive: boolean = false;
  confirmModalDeactive: boolean = false;
  open() {
    this.displayModalAdd = true;
  }


  ngOnInit(): void {
    this.getListNews();
    this.getListType();
  }

  paginate(event) {
    this.page = event.page;
    this.getListNews();
  }

  public getListNews(): void {
    this.dataService.getAllNews(this.page).subscribe((res) => {
      // this.spinner.hide();
      this.news = res.content;
      this.pageSize = res.totalPages;
      this.totalElements = res.totalElements;
      console.log(res);
    }, error => console.log(error));
  }

  public getListType(): void {
    this.dataService.getAllNewsType().subscribe((res) => {
      // this.spinner.hide();
      this.newsType = res.listType;
      console.log(res);
    }, error => console.log(error));
  }

  public deleteNew(): void {
    console.log(this.newsDetail.id)
    this.dataService.deleteNews(this.newsDetail.id).subscribe((res) => {
      if (res.message == "Delete new successfully") {
        this.confirmModal = false;
        this.noti.SuccessNotification(" xa thnh cng !")
      }else{
        this.noti.ErrorNotification("Xa tht bi !")
      }
      console.log(res)
    }, error => console.log(error));
  }

  public addNew() {
    if (this.addNews.type.new_type == "Tin tc") {
      this.addNews.type.id = 1;
    }
    if (this.addNews.type.new_type == "Phong Thu") {
      this.addNews.type.id = 2;
    }
    if (this.addNews.type.new_type == "T vn") {
      this.addNews.type.id = 3;
    }

    this.dataService.addNews(this.addNews).subscribe(res => {
      if (res.message == "Create new NEW successfully") {
        this.displayModalAdd = false;
        this.noti.SuccessNotification("Thm thnh cng !")
      } else {
        this.noti.ErrorNotification("Thm tht bi !")
      }
    }, error => console.log(error));
  }

  public saveNews() {
    if (this.newsDetail.type.new_type == "Tin tc") {
      this.newsDetail.type.id = 1;
    }
    if (this.newsDetail.type.new_type == "Phong Thu") {
      this.newsDetail.type.id = 2;
    }
    if (this.newsDetail.type.new_type == "T vn") {
      this.newsDetail.type.id = 3;
    }
    console.log(this.newsDetail.type.id)
    this.dataService.editNews(this.newsDetail.id, this.newsDetail).subscribe(res => {

      if (res.message == "Update new successfully") {
        this.displayModalEdit = false;
        this.noti.SuccessNotification("Thay i thnh cng !")
      } else {
        this.noti.ErrorNotification("Thay i tht bi !")
      }
    }, error => console.log(error));
  }

}
