import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DataServices } from 'app/core/services/data.service';
import { NotificationService } from 'app/core/services/notification';

@Component({
  selector: 'app-manage-rules',
  templateUrl: './manage-rules.component.html',
  styleUrls: ['./manage-rules.component.css']
})
export class ManageRulesComponent implements OnInit {

  rulesName: string;
  rules: any = [];
  rulesSelect: string;

  rulesDetail: any = {
    id: -1,
    rule: ""
  }
  addRulesDetail: any = {
    rule: ""
  }

  constructor(
    private dataService: DataServices,
    private activeRouter: ActivatedRoute,
    private router: Router,
    private noti: NotificationService) {
    this.getListRules();
  }


  //display

  displayModalView: boolean = false;
  displayModalEdit: boolean = false;
  confirmModal: boolean = false;
  displayModalAdd: boolean = false;
  confirmModalActive: boolean = false;
  confirmModalDeactive: boolean = false;

  open() {
    this.displayModalAdd = true;
  }


  view(id) {
    this.displayModalView = true;
    for (let item of this.rules) {
      if (item.id == id) {
        this.rulesDetail = item;
        return;
      }
    }
  }

  edit(id) {
    this.displayModalEdit = true;
    for (let item of this.rules) {
      if (item.id == id) {
        this.rulesDetail = item;
        this.rulesSelect = item.id;
        return;
      }
    }
  }

  delete(id) {
    this.confirmModal = true;
    for (let item of this.rules) {
      if (item.id == id) {
        this.rulesDetail = item;
        return;
      }
    }
  }

  ngOnInit(): void {
    this.getListRules();
  }

  public getListRules(): void {
    this.dataService.getAllRules().subscribe((res) => {
      this.rules = res.content;
      console.log(res);
    }, error => console.log(error));
  }

  public updateRules() {
    this.dataService.editRules(this.rulesDetail, this.rulesSelect).subscribe(res => {
      if (res.message == "Update rule successfully") {
        this.displayModalEdit = false;
        this.noti.SuccessNotification("Sa iu khon thnh cng !")
      } else {
        this.noti.ErrorNotification("Sa iu khon tht bi !")
      }
    }, error => console.log(error));
  }

  public deleteRules(): void {
    console.log(this.rulesDetail.id)
    this.dataService.deleteRules(this.rulesDetail.id).subscribe((res) => {
      if (res.message == "Delete rule successfully") {
        this.confirmModal = false;
        this.noti.SuccessNotification("Xa iu khon thnh cng !")
      } else {
        this.noti.ErrorNotification("Xa iu khon tht bi !")
      }
      console.log(res)
    }, error => console.log(error));
  }

  public addNewRules() {
    this.dataService.addRules(this.addRulesDetail).subscribe(res => {
      if (res.message == "Create new rule successfully") {
        this.displayModalAdd = false;
        this.noti.SuccessNotification("To iu khon thnh cng !")
      } else {
        this.noti.ErrorNotification("To iu khon tht bi !")
      }
    }, error => console.log(error));
  }

}
