import { Component, OnInit } from '@angular/core';
import { DataServices } from 'app/core/services/data.service';
import { ActivatedRoute, Router } from '@angular/router';
import { NotificationService } from 'app/core/services/notification';

@Component({
  selector: 'app-manage-packages',
  templateUrl: './manage-packages.component.html',
  styleUrls: ['./manage-packages.component.css']
})
export class ManagePackagesComponent implements OnInit {
  packageName: string;
  package: any = [];
  packageSelect: string;
  page: number = 1;
  pageSize: number;
  totalElements: number;

  packageDetail: any = {
    id: -1,
    name: "",
    price: -1,
    description: "",
    type: {
      transaction_type: "",
    },
    package_code: ""
  }

  addpackageDetail: any = {
    name: "",
    price: 0,
    description: "",
    type: {
      id: 0,
      transaction_type: "",
    },
  }
  constructor(
    private dataService: DataServices,
    private activeRouter: ActivatedRoute,
    private router: Router,
    private noti: NotificationService
  ) { }

  //display

  displayModalView: boolean = false;
  displayModalEdit: boolean = false;
  confirmModal: boolean = false;
  displayModalAdd: boolean = false;
  confirmModalActive: boolean = false;
  confirmModalDeactive: boolean = false;
  open1() {
    this.displayModalAdd = true;
  }

  view(id) {
    this.displayModalView = true;
    for (let item of this.package) {
      if (item.id == id) {
        this.packageDetail = item;

        return;
      }
    }

  }
  edit(id) {
    this.displayModalEdit = true;
    for (let item of this.package) {
      if (item.id == id) {
        this.packageDetail = item;
        this.packageSelect = item.id;
        return;
      }
    }
  }
  delete(id) {
    this.confirmModal = true;
    for (let item of this.package) {
      if (item.id == id) {
        this.packageDetail = item;
        return;
      }
    }
  }

  ngOnInit(): void {

    // this.userCurrent=JSON.parse(localStorage.getItem("user"));
    this.getListPackage();
  }

  paginated(event) {
    //event.first = Index of the first record
    //event.rows = Number of rows to display in new page
    //event.page = Index of the new page
    //event.pageCount = Total number of pages
    this.page = event.page + 1;
    this.getListPackage();
  }
  public getListPackage(): void {
    this.dataService.getAllPackage(this.page - 1).subscribe((res) => {
      // this.spinner.hide();
      this.package = res.content;
      this.pageSize = res.totalPages;
      this.totalElements = res.totalElements;
    })
  }

  public savePackage() {
    this.dataService.updatePackage(this.packageDetail, this.packageSelect).subscribe(res => {
      if (res.message == "Update package successfully") {
        this.displayModalEdit = false;
        this.noti.SuccessNotification("Chnh sa gi thnh cng !")
      } else {
        this.noti.ErrorNotification("Chnh sa gi tht bi !")
      }
    }, error => console.log(error));
  }

  public deletePackage(): void {
    console.log(this.packageDetail.id)
    this.dataService.deletePackage(this.packageDetail.id).subscribe((res) => {
      if (res.message == "Delete package successfully") {
        this.confirmModal = false;
        this.noti.SuccessNotification("Xa gi thnh cng !")
      }else{
        this.noti.ErrorNotification("Xa gi tht bi !")
      }
      console.log(res)
    }, error => console.log(error));
  }

  public addNewPackage(){
    console.log(this.addpackageDetail);
    if(this.addpackageDetail.type.transaction_type == "Gi Qung Co"){
      this.addpackageDetail.type.id = 6;
    }
    if(this.addpackageDetail.type.transaction_type == "Gi ti khon"){
      this.addpackageDetail.type.id = 2;
    }
    if(this.addpackageDetail.type.transaction_type == "Gi ng bi"){
      this.addpackageDetail.type.id = 1;
    }
    
    this.dataService.addPackage(this.addpackageDetail).subscribe(res => {
      if (res.message == "Create new package successfully") {
        this.displayModalAdd = false;
        this.noti.SuccessNotification("Thm gi thnh cng !")
      } else{
        this.noti.ErrorNotification("Thm gi tht bi !")
      }
    }, error => console.log(error));
  }
  
}

