import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DataServices } from 'app/core/services/data.service';
import { NotificationService } from 'app/core/services/notification';

@Component({
  selector: 'app-manage-post',
  templateUrl: './manage-post.component.html',
  styleUrls: ['./manage-post.component.css']
})
export class ManagePostComponent implements OnInit {

  post: string;
  posts: any = [];
  postSelect: string;
  page: number = 0;
  pageSize: number;
  totalElements: number;
  result: any = {};
  postDetail: any =
    {
      id: "",
      title: "",
      content: "",
      slug: "",
      contactName: "",
      contactAddress: "",
      contactAddressCity: "",
      contactAddressDistrict: "",
      contactAddressWard: "",
      contactEmail: "",
      contactPhone: "",
      thumnailImage: "",
      status: "",
      approveAt: "",
      price: "",
      pirceUnit: "",
      vip: "",
      account_id: "",
      category: {
        id: "",
        name: "",
        slug: ""
      },
      postImage: [],
      features: [],
      createdAt: "",
      report: []
    }

  constructor(
    private dataService: DataServices,
    private activeRouter: ActivatedRoute,
    private router: Router,
    private noti: NotificationService

  ) { this.getListPost(); }


  //display

  displayModalView: boolean = false;
  displayModalEdit: boolean = false;
  confirmModal: boolean = false;
  confirmModalActive: boolean = false;
  confirmModalDeactive: boolean = false;


  view(id) {
    this.displayModalView = true;
    for (let item of this.posts) {
      if (item.id == id) {
        this.postDetail = item;
        return;
      }
    }
  }

  delete(id) {
    this.confirmModal = true;
    for (let item of this.posts) {
      if (item.id == id) {
        this.postDetail = item;
        return;
      }
    }
  }

  deactive(id) {
    this.confirmModalDeactive = true;
    for (let item of this.posts) {
      if (item.id == id) {
        this.postDetail = item;
        return;
      }
    }
  }

  active(id) {
    this.confirmModalActive = true;
    for (let item of this.posts) {
      if (item.id == id) {
        this.postDetail = item;
        return;
      }
    }
  }

  edit(id) {
    this.displayModalEdit = true;
    for (let item of this.posts) {
      if (item.id == id) {
        this.postDetail = item;
        this.postSelect = item.post;
        return;
      }
    }
  }
  ngOnInit() {
    this.post = this.activeRouter.snapshot.params['post'];
    // this.userCurrent=JSON.parse(localStorage.getItem("user"));
    this.getListPost();

  }

  paginate(event) {
    this.page = event.page;
    this.getListPost();
  }

  public getListPost(): void {
    this.dataService.getAllPost(this.page).subscribe((res) => {
      // this.spinner.hide();
      this.posts = res.content;
      this.pageSize = res.totalPages;
      this.totalElements = res.totalElements;
      console.log(res);
    }, error => console.log(error));
  }

  public updatepost() {
    this.dataService.updatePost(this.postDetail.id).subscribe(res => {
      if (res.message == "Post updated successfully") {
        this.displayModalEdit = false;
        this.noti.SuccessNotification("Thay i post thnh cng !")
      } else {
        this.noti.ErrorNotification("Thay i post tht bi !")
      }
    }, error => console.log(error));
  }

  public deactivePost() {
    this.dataService.deactivePost(this.postDetail.id).subscribe(res => {
      if (res.message == "Deactivate post successfully") {
        this.confirmModalDeactive = false;
        this.noti.SuccessNotification("Hy kch hot thnh cng !")
      } else {
        this.noti.SuccessNotification("Hy kch hot tht bi !")
      }
    }, error => console.log(error));
  }

  public activePost() {
    this.dataService.activePost(this.postDetail.id).subscribe(res => {
      if (res.message == "Active Post successfully") {
        this.confirmModalActive = false;
        this.noti.SuccessNotification("Kch hot thnh cng !")
      } else {
        this.noti.SuccessNotification("Kch hot tht bi !")
      }
    }, error => console.log(error));
  }

}

