import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DataServices } from 'app/core/services/data.service';
import { NotificationService } from 'app/core/services/notification';

@Component({
  selector: 'app-manage-transactions',
  templateUrl: './manage-transactions.component.html',
  styleUrls: ['./manage-transactions.component.css']
})
export class ManageTransactionsComponent implements OnInit {

  transactionName: string;
  transactions: any = [];
  transactionSelect: string;
  page: number = 1;
  pageSize: number;
  totalElements: number;

  transactionDetail: any = {
    id: -1,
    account_name: "",
    transaction_name: "",
    price: -1,
    type: {
      id: -1,
      transaction_type: ""
    },
    description: "",
    createdAt: "",
    status: "",
    transaction_code: ""
  }
  constructor(
    private dataService: DataServices,
    private activeRouter: ActivatedRoute,
    private router: Router,
    private noti: NotificationService
  ) { }

  //display

  displayModalView: boolean = false;
  displayModalEdit: boolean = false;
  confirmModal: boolean = false;
  selected: boolean = true;
  confirmModalActive: boolean = false;
  confirmModalDeactive: boolean = false;

  view(id) {
    this.displayModalView = true;
    for (let item of this.transactions) {
      if (item.id == id) {
        this.transactionDetail = item;
        return;
      }
    }
  }

  edit(id) {
    this.displayModalEdit = true;
    for (let item of this.transactions) {
      if (item.id == id) {
        this.transactionDetail = item;
        this.transactionSelect = item.username;
        return;
      }
    }
  }

  deactive(id) {
    this.confirmModalDeactive = true;
    for (let item of this.transactions) {
      if (item.id == id) {
        this.transactionDetail = item;
        return;
      }
    }
  }

  active(id) {
    this.confirmModalActive = true;
    for (let item of this.transactions) {
      if (item.id == id) {
        this.transactionDetail = item;
        return;
      }
    }
  }
  delete(id) {
    this.confirmModal = true;
    for (let item of this.transactions) {
      if (item.id == id) {
        this.transactionDetail = item;
        return;
      }
    }
  }

  ngOnInit(): void {
    // this.userCurrent=JSON.parse(localStorage.getItem("user"));
    this.getListTranstion();
  }

  paginate(event) {
    //event.first = Index of the first record
    //event.rows = Number of rows to display in new page
    //event.page = Index of the new page
    //event.pageCount = Total number of pages 
    this.page = event.page;
    this.getListTranstion();
  }
  public getListTranstion(): void {
    this.dataService.getAllTransaction(this.page).subscribe((res) => {
      // this.spinner.hide();
      this.transactions = res.content;
      this.pageSize = res.totalPages;
      this.totalElements = res.totalElements;
     
    })
  }

  public deactiveTransactions() {
    this.dataService.deactiveTransaction(this.transactionDetail.id).subscribe(res => {
      if (res.message == "Cancel successfully") {
        this.confirmModalDeactive = false;
        this.noti.SuccessNotification("Hy kch hot thnh cng !")
      } else {
        this.noti.ErrorNotification("Hy kch hot tht bi !")
      }
    }, error => console.log(error));
  }

  public activeTransactions() {
    this.dataService.activeTransaction(this.transactionDetail.id).subscribe(res => {
      if (res.message == "Approve successfully") {
        this.confirmModalActive = false;
        this.noti.SuccessNotification("Kch hot thnh cng !")
      } else {
        this.noti.ErrorNotification("Kch hot tht bi !")
      }
    }, error => console.log(error));
  }

}
